// // // Setup
@import "setup/variables";

//  helpers
@import "helpers/Flex";

//Pages
@import "pages/WeatherDetails";

// Cmps
@import "cmps/AppHeader";
@import "cmps/Search";
@import "cmps/Forcast";

* {
    box-sizing: border-box;
}

html {
    font-size: 18px;
}

body {
    width: 99vw;
    height: 99vh;
    display: flex;
    justify-content: center;
}

.App {
    padding: 10px;
    // border: 1px solid black;
    width: 1200px;
    min-height: 100%;
    // display: flex;
}

.page {
    display: flex;
    // justify-content: center;
    padding: 30px;
    padding-top: 0;
    position: relative;
    z-index: 10;
    flex-direction: column;
    margin-top: 80px;
    .favorite-icon {
        margin: 1px 0;
        fill: red;
        font-size: 2rem;
        cursor: pointer;
        transition: all 0.3s;
        padding: 3px;
        border-radius: 0;
        box-shadow:  0px 0px 0px 0px rgb(134 134 134 / 71%) ;
        &:hover {
            // box-shadow: 0px 0px 20px 0px rgb(134 134 134 / 38%);
            border-radius: 5px;
            background: #d8e1e7;
        }
    }
}

li {
    margin: 0;
    padding: 0;
    list-style: none;
}
ul {
    margin: 0;
    padding: 0;
}
